.home-page {
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  padding: 80px 1.5rem 100px;
  display: flex;
}

.section-title {
  color: var(--text-color);
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
}

.streak-tracker {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

.daily-task-card {
  background: var(--card-background);
  text-align: center;
  box-shadow: 0 10px 30px var(--shadow-light);
  border-radius: 20px;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  overflow: hidden;
}

@keyframes popIn {
  from {
    opacity: 0;
    transform: scale(.5);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.app-header {
  background-color: var(--card-background);
  width: 100%;
  box-shadow: 0 2px 10px var(--shadow-light);
  z-index: 100;
  padding: 1rem 1.5rem;
  position: fixed;
  top: 0;
  left: 0;
}

.calendar-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

#month-year-title {
  flex-grow: 1;
}

#prev-month-btn, #next-month-btn {
  cursor: pointer;
  color: var(--text-color);
  background: none;
  border: none;
  font-size: 1.2rem;
}

.calendar-weekdays {
  text-align: center;
  color: var(--light-text-color);
  grid-template-columns: repeat(7, 1fr);
  margin-bottom: .5rem;
  font-size: .8rem;
  display: grid;
}

#share-calendar-btn {
  border: 1px solid var(--primary-color);
  width: 100%;
  color: var(--primary-color);
  cursor: pointer;
  background: none;
  border-radius: 8px;
  margin-top: 1rem;
  padding: .5rem;
  font-size: .9rem;
}
